h1,
h2,
h3,
h4 {
		font-family: 'Open Sans', Helvetica, sans-serif;
		font-weight: 700;
		text-transform: uppercase;
}

p,
a,
span,
ul,
ol,
li,
h5,
h6 {
		font-family: 'Gentium Basic', Garamond, serif;
}

pre *,
code * {
		font-family: monospace;
}

a {
		-webkit-transition: 0.4s all ease-in-out;
		-moz-transition: 0.4s all ease-in-out;
		transition: 0.4s all ease-in-out;
		color: #1abab5;
}

a:hover, a:active, a:visited {
		color: #159591;
}

::-moz-selection {
		background-color: #1abab5;
		color: #fff;
}

::selection {
		background-color: #1abab5;
		color: #fff;
}

.wrapper {
		max-width: none;
}

nav a {
		font-family: 'Open Sans', Helvetica, sans-serif;
		font-weight: 400;
		letter-spacing: 0.125rem;
		font-size: 1rem;
}

footer .footer-heading {
		letter-spacing: 0.3125rem;
}

footer .social-media-list li a {
		padding: 1.25rem 0;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		line-height: 1;
}

footer .social-media-list li a:first-child {
		padding-top: 0;
}

footer .social-media-list li a .icon {
		margin-right: 10px;
}

footer .final-say {
		font-size: 0.875rem;
		line-height: 1;
}

.site-header {
		position: absolute;
		width: 100%;
		border-bottom: 0px;
}

.site-header .site-title {
		color: #fff;
}

@media (min-width: 601px) {
		.site-header .site-nav .page-link {
				color: #fff;
		}
}

.heading-img {
		width: 2.1875rem;
		margin: 0 auto;
		margin-bottom: 15px;
}

.heading-img img {
		width: 100%;
}

.page-heading,
.heading-img {
		display: none;
}

.page-content {
		padding-top: 0;
}

.home .page-heading {
		margin-bottom: 4.375rem;
		font-size: 90%;
		letter-spacing: 0.3125rem;
}

@media (min-width: 1024px) {
		.home .page-heading {
				margin-bottom: 6.25rem;
		}
}

.home .page-heading {
		text-align: center;
		text-transform: uppercase;
}

.post-list {
		margin-left: -1.875rem;
		margin-right: -1.875rem;
		margin-bottom: -1.875rem;
}

@media (max-width: 800px) {
		.post-list {
				margin-left: -0.9375rem;
				margin-right: -0.9375rem;
		}
}

.post-list > li {
		margin-bottom: 0;
}

html.no-js .post-grid-sizer,
html.no-js .post-item {
		display: inline-block;
		float: left;
}

html.no-js .post-item .post-fill {
		display: none;
}

.post-grid-sizer,
.post-item {
		width: 100%;
}

@media (min-width: 640px) {
		.post-grid-sizer,
		.post-item {
				width: 50%;
		}
}

@media (min-width: 1024px) {
		.post-grid-sizer,
		.post-item {
				width: 33.33333%;
		}
}

.post-item {
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		height: 18.75rem;
		overflow: hidden;
}

.post-item .post-fill {
		-webkit-transition: 0.4s opacity ease-in-out;
		-moz-transition: 0.4s opacity ease-in-out;
		transition: 0.4s opacity ease-in-out;
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		background-color: #000;
		opacity: 0;
		z-index: 1;
}

@media (min-width: 640px) {
		.post-item {
				height: 15.625rem;
		}
}

@media (min-width: 1200px) {
		.post-item {
				height: 18.75rem;
		}
}

@media (min-width: 1440px) {
		.post-item {
				height: 22.5rem;
		}
}

@media (min-width: 1600px) {
		.post-item {
				height: 25rem;
		}
}

@media (min-width: 1920px) {
		.post-item {
				height: 30rem;
		}
}

.post-item.no-bg {
		background-color: #e6e6e6;
}

.post-item .post-link {
		position: relative;
		z-index: 2;
		background: -webkit-linear-gradient(transparent 10%, rgba(0, 0, 0, 0.6) 50%);
		background: linear-gradient(to   , rgba(0, 0, 0, 0.6) 50%);
}

@media (min-width: 640px) {
		.post-item .post-link {
				background: -webkit-linear-gradient(transparent 20%, rgba(0, 0, 0, 0.4) 60%);
				background: linear-gradient(to   , rgba(0, 0, 0, 0.4) 60%);
		}
}

.post-item .post-link.post-wrap {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
}

.post-item .post-link .text-wrap {
		display: block;
		text-align: center;
}

.post-item .post-link h4,
.post-item .post-link .post-meta {
		display: block;
		color: #fff;
		text-transform: uppercase;
}

.post-item .post-link h4 {
		margin-bottom: 0;
		letter-spacing: 0.3125rem;
}

.post-item:hover a, .post-item:active a {
		text-decoration: none;
}

.post-item:hover .post-fill, .post-item:active .post-fill {
		opacity: 0.45;
}

.post-item:hover .post-link.post-wrap, .post-item:active .post-link.post-wrap {
		overflow: hidden;
}

.banner-wrap {
		height: 100%;
		background-color: #f2f2f2;
}

.banner-wrap.inner {
		margin-bottom: 0;
}

.banner-wrap .banner {
		background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.5) 0%, transparent 20%, transparent 40%, rgba(0, 0, 0, 0.7) 90%), url("http://im.vsco.co/1/51f48bb095e8247120/56c9e77a40955b61337b8a75/vsco_022216.jpg");
		background-image: linear-gradient(rgba(0, 0, 0, 0.5) 0%, transparent 20%, transparent 40%, rgba(0, 0, 0, 0.7) 90%), url("http://im.vsco.co/1/51f48bb095e8247120/56c9e77a40955b61337b8a75/vsco_022216.jpg");
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		display: -ms-flex;
		-ms-flex-direction: row;
		-ms-align-items: flex-end;
		-ms-justify-content: center;
		display: -moz-box;
		-moz-flex-direction: row;
		-moz-align-items: flex-end;
		-moz-justify-content: center;
		display: -webkit-box;
		display: -webkit-flex;
		-webkit-flex-direction: row;
		-webkit-align-items: flex-end;
		-webkit-justify-content: center;
		display: flex;
		flex-direction: row;
		align-items: flex-end;
		justify-content: center;
		min-height: 18.75rem;
}

@media (min-width: 640px) {
		.banner-wrap .banner {
				min-height: 25rem;
		}
}

@media (min-width: 1024px) {
		.banner-wrap .banner {
				background-size: 100%;
				min-height: 37.5rem;
		}
}

@media (min-width: 1200px) {
		.banner-wrap .banner {
				min-height: 43.75rem;
		}
}

.banner-wrap .banner .text {
		color: #fff;
		min-width: 250px;
		width: 90%;
		text-align: center;
		margin-bottom: 1.875rem;
}

@media (min-width: 640px) {
		.banner-wrap .banner .text {
				width: 60%;
		}
}

@media (min-width: 1024px) {
		.banner-wrap .banner .text {
				width: 40%;
				margin-bottom: 5rem;
		}
}

.banner-wrap .banner .text a {
		color: #fff;
}

.banner-wrap .banner .text a:hover, .banner-wrap .banner .text a:active {
		text-decoration: none;
		color: #1abab5;
}

.banner-wrap .banner .text h1 {
		margin-bottom: 0.625rem;
		font-size: 1.75rem;
}

@media (min-width: 640px) {
		.banner-wrap .banner .text h1 {
				font-size: 2rem;
		}
}

@media (min-width: 1024px) {
		.banner-wrap .banner .text h1 {
				font-size: 2.25rem;
		}
}

@media (min-width: 1200px) {
		.banner-wrap .banner .text h1 {
				font-size: 2.5rem;
		}
}

.banner-wrap .banner .text h3 {
		letter-spacing: 0.3125rem;
}

@media (min-width: 1200px) {
		.banner-wrap .banner .text h5 {
				font-size: 1.25rem;
		}
}

.banner-wrap.inner .banner {
		min-height: 15.625rem;
}

@media (min-width: 640px) {
		.banner-wrap.inner .banner {
				min-height: 18.75rem;
		}
}

@media (min-width: 1024px) {
		.banner-wrap.inner .banner {
				min-height: 21.875rem;
		}
}

@media (min-width: 1200px) {
		.banner-wrap.inner .banner {
				min-height: 25rem;
		}
}

article {
		max-width: 900px;
		margin: 0 auto;
		margin-top: 90px;
}

article .post-header {
		margin-top: 1.875rem;
		text-align: center;
}

article .post-header .post-title {
		letter-spacing: 0.3125rem;
		margin-bottom: 0.625rem;
}

article .post-header .post-meta {
		margin-bottom: 0.9375rem;
}

article .post-flourish {
		background-blend-mode: overlay;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		width: 1.875rem;
		height: 1.875rem;
		display: block;
		margin: 0 auto;
		opacity: 0.7;
		margin-bottom: 3.75rem;
}

article .post-flourish.top {
		margin-top: 1.875rem;
		width: 2.8125rem;
		height: 2.8125rem;
}

article .post-flourish.bottom {
		-webkit-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		-o-transform: rotate(45deg);
		transform: rotate(45deg);
}

article .post-flourish.plane-up {
		background-image: url("/img/icons/plane-up.svg");
		background-image: url("/img/icons/plane-up.svg");
}

article .post-flourish.japan {
		background-image: url("/img/icons/japan/map-japan.svg");
		background-image: url("/img/icons/japan/map-japan.svg");
}

article .post-flourish.unagi-nigiri {
		background-image: url("/img/icons/japan/unagi-nigiri.svg");
		background-image: url("/img/icons/japan/unagi-nigiri.svg");
}

article .post-flourish.udon {
		background-image: url("/img/icons/japan/udon.svg");
		background-image: url("/img/icons/japan/udon.svg");
}

article .post-flourish.nigiri {
		background-image: url("/img/icons/japan/nigiri.svg");
		background-image: url("/img/icons/japan/nigiri.svg");
}

article .post-flourish.ikura-gunkan-maki {
		background-image: url("/img/icons/japan/ikura-gunkan-maki.svg");
		background-image: url("/img/icons/japan/ikura-gunkan-maki.svg");
}

article .post-flourish.okonomiyaki {
		background-image: url("/img/icons/japan/okonomiyaki.svg");
		background-image: url("/img/icons/japan/okonomiyaki.svg");
}

article .post-flourish.beach {
		background-image: url("/img/icons/beach.svg");
		background-image: url("/img/icons/beach.svg");
}

article .post-flourish.sea {
		background-image: url("/img/icons/sea.svg");
		background-image: url("/img/icons/sea.svg");
}

article .post-flourish.summer {
		background-image: url("/img/icons/summer.svg");
		background-image: url("/img/icons/summer.svg");
}

article .post-flourish.backpack {
		background-image: url("/img/icons/indo/backpack.svg");
		background-image: url("/img/icons/indo/backpack.svg");
}

article .post-flourish.horse {
		background-image: url("/img/icons/indo/horse.svg");
		background-image: url("/img/icons/indo/horse.svg");
}

article .post-flourish.palm-trees {
		background-image: url("/img/icons/indo/palm-trees.svg");
		background-image: url("/img/icons/indo/palm-trees.svg");
}

article .post-flourish.surf {
		background-image: url("/img/icons/indo/surf.svg");
		background-image: url("/img/icons/indo/surf.svg");
}

article .post-flourish.volcano {
		background-image: url("/img/icons/indo/volcano.svg");
		background-image: url("/img/icons/indo/volcano.svg");
}

article h1 {
		margin-bottom: 1.5rem;
}

article p,
article figure {
		margin-bottom: 1.875rem;
}

article li {
		margin-bottom: 0.3125rem;
}

article p img {
		width: 100%;
		opacity: 0;
		-webkit-transform: scale(0);
		-moz-transform: scale(0);
		-ms-transform: scale(0);
		-o-transform: scale(0);
		transform: scale(0);
		-webkit-transition: 0.2s all linear;
		-moz-transition: 0.2s all linear;
		transition: 0.2s all linear;
}

@media (min-width: 768px) {
		article p img {
				-webkit-transition: 0.5s all linear;
				-moz-transition: 0.5s all linear;
				transition: 0.5s all linear;
		}
}

article p img.ready {
		opacity: 1;
		-webkit-transform: scale(1);
		-moz-transform: scale(1);
		-ms-transform: scale(1);
		-o-transform: scale(1);
		transform: scale(1);
}

article p img[data-featherlight] {
		cursor: pointer;
}

@media (min-width: 1024px) {
		article p img:hover[data-featherlight] {
				opacity: 0.8;
		}
}

article .caption {
		font-size: 0.8125rem;
		font-style: italic;
		text-align: left;
}

@media (min-width: 1024px) {
		article .caption {
				font-size: 0.875rem;
				display: block;
				margin-top: 0.3125rem;
		}
}

article .vert-img {
		text-align: center;
}

article .vert-img .caption, article .vert-img.txt {
		text-align: left;
}

article .vert-img .caption {
		display: table;
		margin: 0 auto;
		margin-top: 0.3125rem;
}

@media (min-width: 640px) {
		article .vert-img img {
				width: auto;
				max-height: 500px;
		}
}

@media (min-width: 1024px) {
		article .vert-img img {
				max-height: 600px;
		}
}

@media (min-width: 640px) {
		article .vert-img.col-2-img {
				width: 50%;
				display: inline-block;
				float: left;
		}
		article .vert-img.col-2-img:first-child {
				padding-right: 0.46875rem;
		}
		article .vert-img.col-2-img:last-child {
				padding-left: 0.46875rem;
		}
		article .vert-img.col-2-img img {
				max-height: none;
				width: 100%;
		}
}

article .empty-fill {
		clear: both;
}

html.no-js article p img {
		opacity: 1;
		-webkit-transform: scale(1);
		-moz-transform: scale(1);
		-ms-transform: scale(1);
		-o-transform: scale(1);
		transform: scale(1);
}

.pagination .prev,
.pagination .next {
		font-family: 'Open Sans', Helvetica, sans-serif;
		font-weight: 400;
		letter-spacing: 0.125rem;
		display: inline-block;
		font-size: 0.9375rem;
		text-transform: uppercase;
}

.pagination .prev {
		float: left;
}

.pagination .next {
		float: right;
}

#backtotop {
		-webkit-transition: opacity 0.4s ease-in-out;
		-moz-transition: opacity 0.4s ease-in-out;
		transition: opacity 0.4s ease-in-out;
		color: #fff;
		position: fixed;
		background: rgba(0, 0, 0, 0.8);
		border: solid 1px #000;
		border-radius: 50%;
		width: 4.375rem;
		height: 4.375rem;
		z-index: 9999;
		opacity: 0;
		bottom: 1.25rem;
		right: 1.25rem;
}

@media (min-width: 767px) {
		#backtotop {
				bottom: 2.5rem;
				right: 2.5rem;
		}
}

#backtotop:hover {
		cursor: pointer;
}

#backtotop.show {
		opacity: 1;
}

#backtotop .wrap {
		display: table;
		height: 100%;
		width: 100%;
}

#backtotop span {
		font-family: 'Open Sans', Helvetica, sans-serif;
		font-weight: 400;
		letter-spacing: 0.125rem;
		font-size: 0.875rem;
		text-transform: uppercase;
		display: table-cell;
		vertical-align: middle;
		text-align: center;
}

.featherlight .featherlight-content {
		box-sizing: content-box;
}

.featherlight-next span,
.featherlight-previous span {
		text-shadow: none;
}

@media only screen and (max-width: 767px) {
		.featherlight-next span,
		.featherlight-previous span {
				font-size: 0.75rem;
		}
}
